import { useState, useEffect } from "react";
import PropTypes from "prop-types";

import useMarvelService from "../../services/MarvelService";
import Spinner from "../spinner/Spinner";
import ErrorMessage from "../errorMessage/ErrorMessage";
import Skeleton from "../skeleton/Skeleton";
import CharSearch from "../charSearch/CharSearch";

import ErrorBoundary from "../errorBoundary/ErrorBoundary";

import "./charInfo.scss";

const CharInfo = (props) => {
  const [char, setChar] = useState(null);
  const { loading, error, getCharacter, clearError } = useMarvelService();

  const updateChar = () => {
    const { charId } = props;

    if (!charId) {
      return;
    }

    clearError();
    getCharacter(charId).then(onCharLoaded);
  };

  const onCharLoaded = (char) => {
    setChar(char);
  };

  // componentDidUpdate(prevProps, prevState) {
  //   if (this.props.charId !== prevProps.charId) {
  //     this.updateChar();
  //   }
  //

  useEffect(() => {
    updateChar();
  }, [props]);

  const skeleton = !(char || loading || error) ? <Skeleton /> : null;
  const errorMessage = error ? <ErrorMessage /> : null;
  const spinner = loading ? <Spinner /> : null;
  const content = !(loading || error || !char) ? <View char={char} /> : null;

  return (
    <div className="char__personalInfo">
      <div className="char__info">
        {skeleton} {errorMessage} {spinner} {content}
      </div>
      <ErrorBoundary>
        <CharSearch />
      </ErrorBoundary>
    </div>
  );
};

const View = ({ char }) => {
  const { name, description, thumbnail, homepage, wiki, comics } = char;

  const postListOfComics = (comics) => {
    if (comics.length > 6) {
      comics.length = 6;
    }

    if (comics.length === 0) {
      return <li className="char__comics-item">{"Not comics about this characters"}</li>;
    }

    return comics.map((item, i) => {
      return (
        <li className="char__comics-item" key={i}>
          {item.name}
        </li>
      );
    });
  };

  const comicsList = postListOfComics(comics);

  const image = thumbnail.includes("image_not_available") || thumbnail.includes("gif");

  return (
    <>
      <div className="char__basics">
        <img src={thumbnail} alt={name} style={image ? { objectFit: "fill" } : { objectFit: "cover" }} />
        <div className="char__info-nameWrapper">
          <div className="char__info-name">{name}</div>
          <div className="char__btns">
            <a href={homepage} className="button button__main">
              <div className="inner">homepage</div>
            </a>
            <a href={wiki} className="button button__secondary">
              <div className="inner">wiki</div>
            </a>
          </div>
        </div>
      </div>
      <div className="char__descr">{description}</div>
      <div className="char__comics">Comics:</div>
      <ul className="char__comics-list">{comicsList}</ul>
    </>
  );
};

CharInfo.propTypes = {
  charId: PropTypes.number,
};

export default CharInfo;
