import { Formik, Form, Field, ErrorMessage as ErrorMes } from "formik";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import * as Yup from "yup";

import SmallSpinner from "../spinner/SmallSpinner";
import ErrorMessage from "../errorMessage/ErrorMessage";
import useMarvelService from "../../services/MarvelService";

import "./charSearch.scss";

const CharSearch = () => {
  const [name, setName] = useState("");

  const { loading, error, getRequestOfNames, clearError } = useMarvelService();

  const updateName = () => {
    clearError();
    getRequestOfNames(name)
      .then(() => setName(name))
      .catch(() => {
        setName(false);
      });
  };

  useEffect(() => {
    if (name) {
      updateName();
    }
  }, [name]);

  const ResetName = () => {
    useEffect(() => setName(""));
  };

  const errorMessages =
    name === false ? (
      <span className="errorMain">The character was not found! Check the name and try again!</span>
    ) : null;

  const errorMessage = error ? <ErrorMessage /> : null;
  const spinner = loading ? <SmallSpinner /> : null;
  const content = !(loading || error || !name) ? (
    <>
      <span className="success">{`There is! Visit ${name} page?`}</span>
      <button className="char__search_form-buttonSecondary button button__secondary">
        <div className="inner">
          <Link to={`/character/${name}`}>TO PAGE</Link>
        </div>
      </button>
    </>
  ) : null;

  return (
    <>
      <div className="char__search">
        <Formik
          initialValues={{ name: "" }}
          validationSchema={Yup.object({
            name: Yup.string().required("This field is required").min(2, "At least 2 symbols"),
          })}
          onSubmit={(values) => {
            setName(values.name);
          }}
        >
          {({ errors }) => (
            <Form className="char__search_form">
              <label htmlFor="name" className="char__search_form-label">
                Or find a character by name:
              </label>
              <Field
                id="name"
                name="name"
                type="text"
                placeholder="Enter name"
                className="char__search_form-input"
              ></Field>
              <ErrorMes name="name" component="div" className="error"></ErrorMes>
              <button type="submit" disabled={loading} className="char__search_form-buttonMain button button__main">
                <div className="inner">FIND</div>
              </button>
              {errors.name ? <ResetName /> : errorMessage || errorMessages || content || spinner}
            </Form>
          )}
        </Formik>
      </div>
    </>
  );
};

export default CharSearch;
